@charset "UTF-8";
main {
  padding: 40px 0; }

.articles-popularin,
.articles-headings,
.question,
.articles-publications {
  width: 1060px;
  margin: 0px auto;
  margin: 50px auto; }

.articles-popularin .items {
  background-color: rgba(239, 248, 253, 0.9); }
  .articles-popularin .items:hover * {
    color: #00d381; }

.wrapper-discusses {
  background-image: url(/images/background/doctor.jpg);
  background-position: 31% 89%;
  background-size: cover; }

.articles-discusses {
  width: 880px;
  padding: 40px 0; }
  .articles-discusses-popular {
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: 340px;
    height: 280px;
    background-image: url(/images/background/pattern_green.png);
    font-family: font-serifbold;
    color: #ffffff;
    font-size: 24px;
    margin-right: 40px;
    padding: 3.85em 2.85em;
    box-sizing: border-box;
    font-weight: 700;
    line-height: 30px; }
  .articles-discusses .items {
    flex-basis: calc(100%/2 - 20px);
    padding: 40px 0 0;
    line-height: 18px; }
    .articles-discusses .items a:hover {
      color: #d0e8df; }
    .articles-discusses .items .items-link {
      font-family: "PTSans-Bold";
      color: #ffffff;
      font-size: 18px;
      padding-bottom: 20px; }
    .articles-discusses .items .items-link-discus,
    .articles-discusses .items .items-text {
      font-family: "PTSans-Regular";
      color: #ffffff;
      font-size: 14px; }
    .articles-discusses .items .items-link-discus:after {
      content: "";
      display: block;
      width: 20px;
      height: 1px;
      background-color: #00d381;
      margin: 20px 0; }

.articles-headings .row-item {
  flex-basis: calc(100%/2 - 10px); }
  .articles-headings .row-item .content {
    padding-bottom: 7px; }
  .articles-headings .row-item a:hover {
    color: #77c1ee; }

.articles-headings .columns-left {
  flex: 1 1 0;
  padding-right: 20px; }
  .articles-headings .columns-left .title {
    width: 100%; }
  .articles-headings .columns-left .row-item {
    padding: 0 0 27px 0; }

.articles-headings .row-heading {
  flex: 0 0 520px;
  min-height: 500px;
  background-color: transparent;
  border: 10px #f3fafe solid;
  box-sizing: border-box;
  padding: 0 40px; }
  .articles-headings .row-heading .heading-item {
    flex-basis: 155px; }

.articles-headings .link-article {
  color: #496e86;
  font-size: 16px; }
  .articles-headings .link-article.float {
    display: inline;
    font-size: 14px;
    padding: 0; }

.articles-headings .symbol-link {
  font-size: 14px;
  padding-top: 15px; }
  .articles-headings .symbol-link.left {
    display: inline;
    padding: 0; }
    .articles-headings .symbol-link.left:after {
      padding-left: 10px; }

.articles-headings .symbol-link.right:before,
.articles-headings .symbol-link.left:after {
  content: "ïƒš";
  display: inline-block;
  font-family: "FontAwesome";
  color: #7bbfec;
  font-size: 12px;
  padding-right: 10px; }

.wrapper-publications {
  background-color: #f3fafe; }
  .wrapper-publications .social-link-column {
    width: 250px;
    height: 270px;
    background-image: url(/images/background/pattern_green.png);
    color: #ffffff;
    padding: 39px  20px 0;
    line-height: 24px; }
    .wrapper-publications .social-link-column .title {
      color: #ffffff; }
      .wrapper-publications .social-link-column .title:after {
        border-color: #ffffff;
        margin: 20px auto; }
    .wrapper-publications .social-link-column .title-social-link {
      padding: 10px 0 23px; }
  .wrapper-publications .articles-publications {
    padding: 50px 0; }
    .wrapper-publications .articles-publications .title-main {
      width: 100%;
      text-align: left;
      padding-bottom: 40px; }
      .wrapper-publications .articles-publications .title-main:after {
        content: none; }
  .wrapper-publications .publication-columns {
    flex-wrap: wrap;
    flex: 1 1 0; }
    .wrapper-publications .publication-columns .publication.main {
      flex-basis: calc(100%/2 - 10px);
      padding: 0 0 20px 0;
      margin: 0;
      line-height: 16px; }

.question {
  background-image: url(/images/background/fon_pop_vopros.jpg);
  background-position: center;
  background-size: cover; }
  .question-wrapper {
    width: 880px;
    padding: 50px 0; }
    .question-wrapper .title-main {
      width: 100%;
      background-image: url(/images/background/pattern_green.png);
      color: #ffffff;
      padding: 29px 0;
      margin-bottom: 40px; }
      .question-wrapper .title-main:after {
        background-image: -webkit-linear-gradient(left, #fff, #ffffff, #fff);
        background-image: -moz-linear-gradient(left, #fff, #ffffff, #fff);
        background-image: -ms-linear-gradient(left, #fff, #ffffff, #fff);
        background-image: -o-linear-gradient(left, #fff, #ffffff, #fff); }
    .question-wrapper ul {
      flex-basis: calc(100%/5 - 20px); }
      .question-wrapper ul:last-child {
        padding-right: 0; }
      .question-wrapper ul a {
        display: block;
        font-family: "PTSans-Regular";
        color: #496e86;
        font-size: 16px;
        padding-bottom: 15px; }
        .question-wrapper ul a:hover {
          color: #00d381; }
